.pokecard {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border: 3px solid #a63d40;
  border-radius: 10px;
  width: 15em;
  background-color: #90a959;
  transition: ease-out 0.1s transform;
}

.pokecard:hover,
.pokecard:focus {
  border-color: #151515;
  background-color: #a63d40;
  color: #e9b872;
  transform: scale(1.1);
}

.name {
  text-transform: uppercase;
  letter-spacing: 2px;
  font-size: 1.2rem;
  font-weight: 500;
}
